// Details.js
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Col, Row, Container } from '../../components/Grid';
import Jumbotron from '../../components/Jumbotron';
import API from '../../utils/API';

class Empleado extends Component {
	state = {
		empleado: {}
	};

	componentDidMount() {
		API.getEmpleado(this.props.match.params.id)
			.then(res => this.setState({ empleado: res.data }))
			.catch(err => console.log(err));
	}

	render() {
		return (
			<Container fluid>
				<Row>
					<Col size="md-12">
						<Jumbotron>
							<h1>
								{this.state.empleado.nombre} {this.state.empleado.apellido1} {this.state.empleado.apellido2} 
							</h1>
						</Jumbotron>
					</Col>
				</Row>

				<Row>
					<Col size="md-2">
						<Link to="/empleados">‚Üê Volver a Empleados</Link>
					</Col>
				</Row>
			</Container>
		);
	}
}

export default Empleado;