// Empleados.js

import React, { Component } from 'react';
import { Menu, Dropdown, Button, message, Tooltip } from 'antd';
import { DownOutlined, UserOutlined } from '@ant-design/icons';
import DeleteBtn from '../../components/DeleteBtn';
import Jumbotron from '../../components/Jumbotron';
import API from '../../utils/API';
import { Link } from 'react-router-dom';
import { Col, Row, Container } from '../../components/Grid';
import { List, ListItem } from '../../components/List';
import { Input, FormBtn, Select, SelectItem } from '../../components/Form';

class Empresas extends Component {
	state = {
		empresas: [],
		empleados:[],
		nombre: '',
		responsable: ''
	};

	componentDidMount() {
		this.loadEmpresas();
	}

	loadEmpresas = () => {
		API.getEmpresas()
			.then(res => this.setState({empresas: res.data}))
			.catch(err => console.log(err));
	};

	loadEmpleados = () =>{
		API.getEmpleados()
			.then(res => this.setState({empleados: res.data}))
			.catch(err => console.log(err));
	}

	deleteEmpresa = id => {
		API.deleteEmpresa(id)
			.then(res => this.loadEmpresas())
			.catch(err => console.log(err));
	};

	handleInputChange = event => {
		const { name, value } = event.target;
		this.setState({
			[name]: value
		});
	};

	handleFormSubmit = event => {
		event.preventDefault();
		if (this.state.nombre && this.state.responsable) {
			API.saveEmpresa({
				nombre: this.state.nombre,
				responsable: this.state.responsable,
				fechaAlta: Date.now().toString()
			})
				.then(res => this.loadEmpresas())
				.catch(err => console.log(err));
		}
	};

	handleButtonClick(e) {
		message.info('Click on left button.');
		console.log('click left button', e);
	  }
	  
	handleMenuClick(e) {
		message.info('Click on menu item.');
	  }
	  
	menu = (
		<Menu onClick={this.handleMenuClick}>
			{this.state.empleados.map(empleado => (
				<Menu.Item key={empleado._id} icon={<UserOutlined />}>
					{empleado.nombre}
			  	</Menu.Item>
				))}
		</Menu>
	  );

	render() {
		return (
			<Container fluid>
				<Jumbotron>
					<Container fluid>
						<h1>Empresas</h1>
						{this.state.empresas.length ? (
							<List>
								{this.state.empresas.map(empresa => (
									<ListItem key={empresa._id}>
										<Link to={'/empresas/' + empresa._id}>
											<strong>
												{empresa.nombre} {empresa.responsable.nombre}
											</strong>
										</Link>
										<Button danger onClick={() => this.deleteEmpresa(empresa._id)} />
									</ListItem>
								))}
							</List>
						) : (
							<h3>Sin resultados</h3>
						)}
					</Container>
				</Jumbotron>
				<Jumbotron>
					<Container fluid>
						<h1>Empresa</h1>
							<form>
							<Input
								value={this.state.nombre}
								onChange={this.handleInputChange}
								name="nombre"
								placeholder="Nombre (required)"
							/>
							<Dropdown.Button onClick={this.handleButtonClick} overlay={this.menu}>
    						  Responsable
    						</Dropdown.Button>
							<Input
								value={this.state.responsable}
								onChange={this.handleInputChange}
								name="responsable"
								placeholder="Reponsable (required)"
							/>
							<FormBtn
								disabled={!(this.state.nombre && this.state.responsable)}
								onClick={this.handleFormSubmit}
							>
								Guardar Empresa
							</FormBtn>
						</form>
					</Container>
				</Jumbotron>

			</Container>
		);
	}
}

export default Empresas;