// Empleados.js

import React, { Component } from 'react';
//import DeleteBtn from '../../components/DeleteBtn';
//import Jumbotron from '../../components/Jumbotron';
import API from '../../utils/API';
//import { Link } from 'react-router-dom';
//import { Col, Row, Container } from '../../components/Grid';
//import { List, ListItem } from '../../components/List';
//import { Input, FormBtn } from '../../components/Form';
import { Table, Space, Layout, Form, Input, Anchor, Button, Typography } from 'antd';
const { Column, ColumnGroup } = Table;
const { Link } = Anchor;
const { Content } = Layout;
const { Title } = Typography;
class Empleados extends Component {
	state = {
		empleados: [],
		nombre: '',
		apellido1: '',
		apellido2:''
	};

	layout = {
		labelCol: { span: 8 },
		wrapperCol: { span: 16 },
	};

	tailLayout = {
		wrapperCol: { offset: 8, span: 16 },
	};


	columns = [
		{
			title : 'Nombre',
			dataIndex: 'nombre',
			key: 'nombre'
		},
		{
			title: 'Acci√≥n',
			key: 'action',
			render: (text, record) => (
			  <Space size="middle">
				<Anchor>
					<Link href={'/empleados/' + record._id} title="Editar"></Link>
				</Anchor>
				<Anchor onClick={() => this.deleteEmpleado(record._id)}>Borrar</Anchor>
			  </Space>
			),
		  },
	]

	onFinish = values => {
		console.log('Success:', values);
	};
	
	onFinishFailed = errorInfo => {
		console.log('Failed:', errorInfo);
	};
	componentDidMount() {
		this.loadEmpleados();
	}

	loadEmpleados = () => {
		API.getEmpleados()
			.then(res => this.setState({empleados: res.data}))
			.catch(err => console.log(err));
	};

	deleteEmpleado = id => {
		API.deleteEmpleado(id)
			.then(res => this.loadEmpleados())
			.catch(err => console.log(err));
	};

	handleInputChange = event => {
		const { name, value } = event.target;
		this.setState({
			[name]: value
		});
	};

	handleFormSubmit = event => {
		event.preventDefault();
		if (this.state.nombre && this.state.apellido1 && 
			this.state.apellido2) {
			API.saveEmpleado({
				nombre: this.state.nombre,
				apellido1: this.state.apellido1,
				apellido2: this.state.apellido2,
				fechaAlta: Date.now().toString()
			})
				.then(res => this.loadEmpleados())
				.catch(err => console.log(err));
		}
	};

	render() {
		return (
			<Layout>
				<Content>
					<Title level={4}>Empleados</Title>
					<Table columns={this.columns} dataSource={this.empleados} />
				</Content>
				<Content>
					<Title level={4}>Empleado</Title>
					<Form 
						{...this.layout} 
						name="basic" 
						initialValues={{ remember: true }}
      					onFinish={this.onFinish}
      					onFinishFailed={this.onFinishFailed}
    				>
						<Form.Item
        					label="Nombre"
        					name="nombre"
        					rules={[{ required: true, message: 'Introduzca un nombre' }]}
      					>
							<Input 
								placeholder="Nombre (requerido)"
								value={this.state.nombre}
								onChange={this.handleInputChange}
							/>
      					</Form.Item>
						  <Form.Item
        					label="Apellido 1"
        					name="apellido1"
        					rules={[{ required: true, message: 'Introduzca el primer apellido' }]}
      					>
							<Input 
								placeholder="Apellido 1 (requerido)"
								value={this.state.apellido1}
								onChange={this.handleInputChange}
							/>
      					</Form.Item>
						  <Form.Item
        					label="Apellido 2"
        					name="apellido2"
        					rules={[{ required: true, message: 'Introduzca el segundo apellido' }]}
      					>
							<Input 
								placeholder="Apellido 2 (requerido)"
								value={this.state.apellido2}
								onChange={this.handleInputChange}
							/>
      					</Form.Item>

						<Form.Item {...this.tailLayout}>
							<Button 
								type="primary" 
								htmlType="submit" 
								disabled={!(this.state.apellido1 && this.state.apellido2)}
								onClick={this.handleFormSubmit}
								>
          						Enviar
      		  				</Button>
      					</Form.Item>
					</Form>
				</Content>
			</Layout>
		);
	}
}

export default Empleados;